<template>
  <v-dialog v-model="dialog" persistent max-width="600px">
    <template v-slot:activator="{ on, attrs }">
      <!-- build button for add Area  -->
      <v-btn
        class="ma-2"
        outlined
        medium
        v-bind="attrs"
        v-on="on"
        color="primary"
      >
        <v-col cols="12"> <v-icon>mdi-plus</v-icon> add Area </v-col>
      </v-btn>
      <!-- end  build button for add Area  -->
    </template>
    <v-form ref="form" lazy-validation v-model="valid">
      <v-card>
        <!-- title card dialog  -->
        <v-card-title>
          <span class="text-h5">Add Area</span>
        </v-card-title>
        <!--  -->
        <v-card-text>
          <v-container>
            <v-row>
              <!-- Name field card  -->
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="Area Name Ar *"
                  required
                  v-model="area.name.ar"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="Area Name En *"
                  required
                  v-model="area.name.en"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="Area Name Tr *"
                  required
                  v-model="area.name.tr"
                ></v-text-field>
              </v-col>
              <!--  -->

              <!-- Description field card    -->
              <!-- <v-col cols="12">
                <v-textarea
                  label="area Description*"
                  v-model="area.description"
                  :rules="descriptionRules"
                >
                </v-textarea>
              </v-col>
              -->
            </v-row>
          </v-container>
          <small color="red">*indicates required field</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="dialog = false"> Cancel </v-btn>
          <v-btn color="secondary" text @click="addArea">
            <span v-if="!progressAddArea">Add Area</span>
            <v-progress-circular
              v-if="progressAddArea"
              color="primary"
              indeterminate
            ></v-progress-circular>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-form>
  </v-dialog>
</template>
<script>
import { areaController } from "./area";
export default {
  data() {
    return {
      // form
      valid: true,
      // validation form
      nameRules: [(v) => !!v || "Name is required"],

      area: {
        name: {},
      },

      //
      progressAddArea: false,
      isUpdating: false,
      dialog: this.dialogController,
      allAreas: [],
      //  selectedAreas contains the id of areas from send this ids to server ..
    };
  },

  watch: {
    isUpdating(val) {
      if (val) {
        setTimeout(() => (this.isUpdating = false), 3000);
      }
    },
  },

  methods: {
    addArea: async function () {
      if (this.$refs.form.validate()) {
        this.progressAddArea = true;
        var state = await areaController.addArea(this.area);
        if (state) {
          this.dialog = false;
          this.restData();
        }
        this.progressAddArea = false;
      }
    },

    restData() {
      this.$emit("getVal");
      this.$refs.form.reset();
      this.area = {
        name: {},
      };
    },
  },
};
</script>
 